import InputText from 'primevue/inputtext';
import Textarea from 'primevue/textarea';

export default {
  name: 'Landing',
  components: {
    Menubar, Button, Card, InputText, Textarea
  },
  data() {
    return {
        scrollPos: 0,
        show: true,
        mobile: false,
        topMenu: [
          {
            label: "Home",
            icon: "pi pi-fw pi-home",
            url: "#home",
            class: "my-menu"
          },
          {
            label: "Tentang Kami",
            icon: "pi pi-fw pi-info-circle",
            url: "#intro",
            class: "my-menu"
          },
          {
            label: "Partner Strategik",
