            url: "#misi",
            class: "my-menu"
          },
          {
            label: "Kontak",
            icon: "pi pi-fw pi-envelope",
            url: "#kontak",
            class: "my-menu"
          },
            
        ],
        contact: {
          name: "",
          email: "",
          message: ""
        }
    }
  },
  created() {
    window.addEventListener('scroll', this.updateScroll);
    this.isMobile();
  },
  mounted() {

  },
  methods: {
    updateScroll() {
      this.scrollPos = window.scrollY      
    },
    isMobile() {
      if (screen.width <= 540)
        this.mobile = true;
    },
    goTo(routeName) {
      this.$router.push({name: routeName})
    }
    
  }
}
</script>

